import {
  getSyslogList,
} from '../../utils/service';
import {
  DxSelection,
  DxDataGrid,
  DxColumn,
  DxFilterRow,
  DxPager,
  DxFilterPanel,
  DxPaging,
  DxHeaderFilter,
  DxScrolling
} from "devextreme-vue/data-grid";
import zhMessages from "devextreme/localization/messages/zh.json";
import {
  locale,
  loadMessages
} from "devextreme/localization";

export default {
  created() {
    loadMessages(zhMessages);
    locale(navigator.language);
  },
  components: {
    DxSelection,
    DxDataGrid,
    DxColumn,
    DxFilterRow,
    DxPager,
    DxFilterPanel,
    DxPaging,
    DxHeaderFilter,
    DxScrolling
  },
  data() {
    return {
      datalist: '',
      total: 0,
      pagesize: 20,
      nowDate: '',
      description: '',
      success: '',
      timedata: '',
      pickerOptions: {
        shortcuts: [{
          text: '最近一周',
          onClick(picker) {
            const end = new Date();
            const start = new Date();
            start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
            picker.$emit('pick', [start, end]);
          }
        }, {
          text: '最近一个月',
          onClick(picker) {
            const end = new Date();
            const start = new Date();
            start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
            picker.$emit('pick', [start, end]);
          }
        }, {
          text: '最近三个月',
          onClick(picker) {
            const end = new Date();
            const start = new Date();
            start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
            picker.$emit('pick', [start, end]);
          }
        }]
      },
      status: [{
        "label": "全部",
        "value": 2
      }, {
        "label": "失败 ",
        "value": 0
      }, {
        "label": "成功",
        "value": 1
      }]
    };
  },
  created() {

  },
  mounted() {
    this.getSearchDatalist()

  },
  methods: {
    newdata() {
      const date = new Date();
      const year = date.getFullYear();
      const month = date.getMonth() + 1;
      const day = date.getDate();
      this.month = check(month);
      this.day = check(day);

      function check(i) {
        const num = (i < 10) ? ("0" + i) : i;
        return num;
      }
      this.nowDate = year + "-" + this.month + "-" + this.day;
    },
    // 初始化数据
    getSearchDatalist() {
      this.newdata()
      var formData = {
        pageindex: 1,
        pagesize: this.pagesize,
        startdate: this.nowDate
      }
      getSyslogList(formData, res => {
        this.total = res.count
        this.datalist = res.data
        if (this.datalist) {
          this.datalist.map((x, index) => {
            x.key = index + 1;
          });
        }
      });
    },
    // 页码数改变
    handleCurrentChange(e) {
      var formData = {
        pageindex: e,
        pagesize: this.pagesize,
      }
      getSyslogList(formData, res => {
        this.total = res.count
        this.datalist = res.data
        this.datalist.map((x, index) => {
          x.key = index + 1;
        });
      });
    },
    handleSizeChange(e) {
      this.pagesize = e
    },
    changesdevicetype(value) {
      this.success = value
    },
    topsure() {
      var startdate = ''
      var enddate = ''
      for (let index = 0; index < this.timedata.length; index++) {
        startdate = this.timedata[0];
        enddate = this.timedata[1];
      }
      var formData = {
        pageindex: 1,
        pagesize: this.pagesize,
        startdate: startdate,
        enddate: enddate,
        description: this.description,
        success: this.success

      }
      getSyslogList(formData, res => {
        this.total = res.count
        this.datalist = res.data
        if (this.datalist) {
          this.datalist.map((x, index) => {
            x.key = index + 1;
          });
        }
      });
    }
  }
};



