<template>
  <div class="vibration">
    <div class="bg">
      <div class="input">
        <div class="choice" style="width: 1500px;">
          <el-row>
            <el-col :span="6">
              <span>日期范围：</span>
              <el-date-picker
                v-model="timedata"
                type="daterange"
                align="right"
                start-placeholder="开始日期"
                value-format="yyyy-MM-dd"
                end-placeholder="结束日期"
                :picker-options="pickerOptions"
              >
              </el-date-picker>
            </el-col>
            <el-col :span="4">
              <span>成功状态：</span>
              <el-select
                v-model="success"
                clearable
                placeholder="请选择"
                @change="changesdevicetype"
              >
                <el-option
                  v-for="(item, i) in status"
                  :key="i"
                  :label="item.label"
                  :value="item.value"
                >
                </el-option>
              </el-select>
            </el-col>
            <el-col :span="8">
              <span class="sbname">日志描述： </span>
              <el-input v-model.trim="description" placeholder="请输入日志描述">
              </el-input>
            </el-col>
            <el-col :span="2">
              <el-button
                type="success"
                plain
                size="medium"
                class="button"
                @click="topsure"
                >确认</el-button
              >
            </el-col>
          </el-row>
        </div>
      </div>
      <DxDataGrid
        :data-source="datalist"
        :allow-column-resizing="true"
        :show-column-lines="false"
        :auto-expand-all="false"
        :show-row-lines="true"
        :column-auto-width="true"
        :focused-row-enabled="true"
        :auto-navigate-to-focused-row="true"
        width="98%"
        key-expr="key"
        class="main_table"
        style="height: calc(335px + 42vh);width:98%"
      >
        <DxColumn
          :width="380"
          data-field="insdate"
          alignment="center"
          caption="日志日期"
        />
        <DxColumn
          :width="380"
          data-field="logtype"
          alignment="center"
          caption="模块名称"
        />
        <DxColumn
          :width="380"
          data-field="description"
          alignment="center"
          caption="日志描述"
        />
        <DxColumn
          :width="380"
          data-field="staff"
          alignment="center"
          caption="操作人员"
        />
        <DxScrolling use-native="false" />
      </DxDataGrid>
      <el-pagination
        :page-size="pagesize"
        :pager-count="5"
        :page-sizes="[10, 20, 30, 40]"
        @size-change="handleSizeChange"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
        @current-change="handleCurrentChange"
      >
      </el-pagination>
    </div>
  </div>
</template>

<script>
import log from "./log";
export default log;
</script>
<style scoped>
@import url("./log.css");
@import url("../../assets/css/common.css");
</style>



